"use client";
import React, { useState, useMemo } from "react";
import { FiSearch, FiFilter, FiAward, FiUser, FiLayers } from "react-icons/fi";
import { honoreesData, Honoree } from "../../componets/Honorees/data";
import { generateSlug } from "../../componets/Honorees/slugUtils";
import { getContent } from "../../content/content";
import { useLanguage } from "../../context/LanguageContext";
import "../../componets/styles/honorees.css";
import Footer from "../../componets/footer/Footer";
import Header from "../../componets/header/Header";
import { navigate } from "gatsby";

const HonoreesPage: React.FC = () => {
  const { lang } = useLanguage();
  const honoreesContent = getContent(lang).pages.honorees?.section || {
    pageHeader: {
      title: lang === "en" ? "All Honorees" : "Tất cả Danh hiệu",
      description: lang === "en" ? "Explore Vietnam's recognized individuals and organizations of Quintessence" : "Khám phá những cá nhân và tổ chức được công nhận Tinh hoa Việt Nam"
    },
    filters: {
      searchPlaceholder: lang === "en" ? "Search by name, field, or region..." : "Tìm kiếm theo tên, lĩnh vực hoặc khu vực...",
      categoryFilter: {
        all: lang === "en" ? "All Categories" : "Tất cả Danh mục"
      }
    },
    noResults: lang === "en" ? "No honorees found." : "Không tìm thấy danh hiệu nào.",
    certificateLabel: lang === "en" ? "Certificate:" : "Chứng nhận:",
    achievementsLabel: lang === "en" ? "Achievements:" : "Thành tựu:"
  };
  
  const [search, setSearch] = useState("");
  const [filterType, setFilterType] = useState("All");
  const [filterCategory, setFilterCategory] = useState("All");

  const categories = Array.from(new Set(honoreesData.map(h => h.category)));
  const types = ["All", "Individual", "Organization"];

  const filteredData = useMemo(() => {
    return honoreesData.filter((h) => {
      const matchesSearch =
        h.name.toLowerCase().includes(search.toLowerCase()) ||
        h.fieldOfEstablishment.toLowerCase().includes(search.toLowerCase()) ||
        h.region.toLowerCase().includes(search.toLowerCase());
      const matchesType = filterType === "All" || h.type === filterType;
      const matchesCategory =
        filterCategory === "All" || h.category === filterCategory;
      return matchesSearch && matchesType && matchesCategory;
    });
  }, [search, filterType, filterCategory]);

  return (
    <div>
        <Header/>
    <section className="honorees-page">
      <div className="page-header">
        <h1>
          <FiAward /> {honoreesContent.pageHeader.title}
        </h1>
        <p>Explore Vietnam’s recognized individuals and organizations of Quintessence</p>
      </div>

      {/* Filters */}
      <div className="filters">
        <div className="search-box">
          <FiSearch className="search-icon" />
          <input
            type="text"
            placeholder={honoreesContent.filters.searchPlaceholder}
            value={search}
            onChange={(e) => setSearch(e.target.value)}
          />
        </div>

        <div className="select-group">
          <div className="filter">
            <FiUser className="filter-icon" />
            <select value={filterType} onChange={(e) => setFilterType(e.target.value)}>
              {types.map((t) => (
                <option key={t} value={t}>{t}</option>
              ))}
            </select>
          </div>

          <div className="filter">
            <FiLayers className="filter-icon" />
            <select
              value={filterCategory}
              onChange={(e) => setFilterCategory(e.target.value)}
            >
              <option value="All">{honoreesContent.filters.categoryFilter.all}</option>
              {categories.map((c) => (
                <option key={c} value={c}>{c}</option>
              ))}
            </select>
          </div>
        </div>
      </div>

      {/* Honorees Grid */}
      <div className="honorees-grid">
        {filteredData.length === 0 ? (
          <p className="no-results">{honoreesContent.noResults}</p>
        ) : (
          filteredData.map((h: Honoree, i: number) => (
            <div 
              key={i} 
              className="honoree-card-large cursor-pointer hover:transform hover:scale-105 transition-all duration-300"
              onClick={() => navigate(`/honorees/${generateSlug(h.name)}`)}
            >
              <div className="honoree-img">
                <img src={h.image} alt={h.name} />
              </div>
              <div className="honoree-details">
                <h3 className="honoree-name">{h.name}</h3>
                <p className="honoree-meta">
                  {h.type} • {h.category}
                </p>
                <p className="honoree-field">{h.fieldOfEstablishment}</p>
                <p className="honoree-region">{h.region}</p>
                <p className="honoree-cert">{honoreesContent.certificateLabel} {h.certificateCode}</p>
                <ul className="honoree-key-figures">
                  {h.keyFigures.map((kf, idx) => (
                    <li key={idx}>{kf}</li>
                  ))}
                </ul>
                <div className="honoree-achievements">
                  <h4>{honoreesContent.achievementsLabel}</h4>
                  {h.achievements.map((a, idx) => (
                    <p key={idx}>
                      <strong>{a.year}:</strong> {a.title} – {a.description}
                    </p>
                  ))}
                </div>
                <div className="mt-4 text-center">
                  <span className="text-yellow-400 text-sm font-semibold">
                    {lang === "en" ? "Click to view profile →" : "Nhấp để xem hồ sơ →"}
                  </span>
                </div>
              </div>
            </div>
          ))
        )}
      </div>
    </section>
    <Footer/>
    </div>
  );
};

export default HonoreesPage;